@import "ScssHelpers/helpers-all.scss";

.advantages_line {
	background-color: #f5f5f5;

	@include bp($point_4, min) {
		padding-top: 2.4rem;
		padding-bottom: 2.8rem;
	}

	@include bp($point_4 - 1) {
		padding-top: 2rem;
		padding-bottom: 2.4rem;
	}
}

.advantages_line__list {
	@include flex_block(row, wrap, center, center);

	@include bp($point_4, min) {
		margin: 0 -4rem -3rem 0;
	}

	@include bp($point_4 - 1) {
		margin-bottom: -1.2rem;
	}
}

.advantages_line__list_item {
	@include bp($point_4, min) {
		width: 25%;
		min-width: 30rem;
		padding: 0 4rem 3rem 0;
	}

	@include bp($point_4 - 1) {
		width: 50%;
		padding-bottom: 1.2rem;
	}
}

.advantages_line__item {
	position: relative;

	display: flex;
	align-items: center;
	justify-content: flex-start;

	@include bp($point_2, min) {
		.advantages_line__list_item:not(:last-child) & {
			&::after {
				position: absolute;
				right: 0;
				top: 0;

				content: "";

				width: 1px;
				height: 100%;

				background-color: $white;
			}
		}
	}

	@include bp($point_4, min) {
		padding-right: 2rem;
	}

	@include bp($point_4 - 1) {
		padding-right: 0.6rem;
	}
}

.advantages_line__item_img {
	flex-shrink: 0;

	border-radius: 50%;
	overflow: hidden;
	mix-blend-mode: darken;

	img {
		@include cover_img;
	}

	@include bp($point_4, min) {
		width: 8.6rem;
		height: 8.6rem;

		&:not(:last-child) {
			margin-right: 2rem;
		}
	}

	@include bp($point_4 - 1) {
		width: 5.2rem;
		height: 5.2rem;

		&:not(:last-child) {
			margin-right: 1rem;
		}
	}
}

.advantages_line__item_title {
	width: 100%;

	font-weight: 500;

	@include bp($point_4, min) {
		@include text;
		line-height: 1.25;
	}

	@include bp($point_4 - 1) {
		font-size: 1.2rem;
		line-height: 1.16;
		letter-spacing: -0.016em;
	}
}
