@import "ScssHelpers/helpers-all.scss";

.notify_form {
	margin-bottom: 2.6rem;
}

.notify_form__title {
	margin-bottom: 1.8rem;
	font-size: 1.6rem;
	font-weight: 500;
	letter-spacing: .002em;
	line-height: 2.4rem;
	color: $black;
}

.notify_form__group {
	display: flex;
	align-items: center;
	justify-content: space-between;
	flex-wrap: wrap;
	position: relative;
}

.variation_form .form_select__element {
	& .ts-control {
		box-shadow: inset 0 0 0 1px $gray_2;
	}
}

.notify_form .form_input {
	width: 100%;
}

.notify_form  .form_input__element {
	padding: 0rem 4.6rem 0.2rem 1.9rem;
	padding-right: 13rem;
}

.notify_form .form_input__element::-webkit-input-placeholder {
	color: $black;
}

.notify_form .form_input__element::-moz-placeholder:not(:focus) {
	opacity: 1;
}
.notify_form .form_input__element {
	&:focus {
		&::-webkit-input-placeholder {
			opacity: 0;
		}
	}
}

.notify_form button {
	position: absolute;
	right: 0;
	top: 0;
}