@import "ScssHelpers/helpers-all.scss";

.product_content {
	// padding-top: 0.8rem;

	@include bp($point_3) {
		padding: 0 var(--gl-indent);
	}
}

.product_content__head {
	padding-bottom: 3.4rem;
	border-bottom: 1px solid $beige_2;

	&:not(:last-child) {
		margin-bottom: 2rem;
	}

	@include bp($point_3) {
		display: none;
	}
}

.product_content__title {
	font-size: 4.8rem;
	color: $black;
	font-weight: 500;
	letter-spacing: -0.02em;
	line-height: 1.4;

	&:not(:last-child) {
		margin-bottom: 0.4rem;
	}
}

.product_content__subtitle {
	font-size: 1.8rem;
	font-weight: 500;
	color: $gray_2;

	letter-spacing: 0.11em;
	line-height: 1.33;
	text-transform: uppercase;

	&:not(:last-child) {
		margin-bottom: 0.4rem;
	}
}

.product_content__excerpt {
	@include text;

	border-bottom: 1px solid $beige_2;
	color: $black;
	line-height: 1.5;

	@include bp($point_2, min) {
		padding-bottom: 2.8rem;

		&:not(:last-child) {
			margin-bottom: 2.8rem;
		}
	}

	@include bp($point_3) {
		padding-top: 1.6rem;
		padding-bottom: 1.6rem;

		border-top: 1px solid $beige_2;

		font-size: 1.4rem;
		line-height: 1.42;
		letter-spacing: -0.014em;
		color: rgba(0, 0, 0, 0.8);

		&:not(:last-child) {
			margin-bottom: 2.4rem;
		}
	}
}

.product_content__excerpt b {
	display: block;

	font-weight: 500;
	color: $black;

	@include bp($point_4, min) {
		margin-bottom: 0.6rem;
		font-size: 1.8rem;
	}

	@include bp($point_4 - 1) {
		margin-bottom: 0.4rem;
		font-size: 1.5rem;
		line-height: 1.33;
		letter-spacing: -0.034em;
	}
}

.product_content__price_wrapper {
	@include bp($point_4, min) {
		display: flex;
		align-items: center;
		justify-content: flex-start;
		flex-wrap: wrap;

		margin: 0 -2rem -0.4rem 0;
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 1.4rem;
		}
	}
}

.product_content__price {
	color: $black;

	margin-right: auto;

	@include bp($point_4, min) {
		padding: 0 2rem 2.6rem 0;
		font-size: 3.2rem;
	}

	@include bp($point_4 - 1) {
		font-size: 2.2rem;

		&:not(:last-child) {
			margin-bottom: 1.2rem;
		}
	}
}

.product_content__old_price {
	font-weight: 300;
	color: $gray_2;

	@include bp($point_4, min) {
		&:not(:last-child) {
			margin-right: 2.2rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-right: 1.6rem;
		}
	}
}

.product_content__price_info {
	display: flex;
	align-items: center;
	justify-content: flex-start;
	flex-wrap: nowrap;

	font-weight: 500;
	letter-spacing: -0.014em;
	line-height: 1.14;
	color: #044e31;

	@include bp($point_4, min) {
		padding: 0 2rem 2.4rem 0;
		font-size: 1.4rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.3rem;

		&:not(:last-child) {
			margin-bottom: 1.8rem;
		}
	}
}

.product_content__price_info_icon {
	flex-shrink: 0;
	display: flex;
	align-items: center;
	justify-content: center;

	border-radius: 100%;
	color: $white;
	background-color: #044e31;

	@include bp($point_4, min) {
		width: 3.4rem;
		height: 3.4rem;
		padding: 4px;

		&:not(:last-child) {
			margin-right: 1.4rem;
		}
	}

	@include bp($point_4 - 1) {
		width: 2.8rem;
		height: 2.8rem;
		padding: 3px;

		&:not(:last-child) {
			margin-right: 1rem;
		}
	}
}

.product_content__price_info_text {
	max-width: 26rem;
}

.variation_form {

	@include bp($point_2, min) {
		padding-bottom: 4rem;
	}

	@include bp($point_3) {
		padding-bottom: 2rem;
	}
}

table.variations {
	width: 100%;
	text-align: left;

	tr {
		display: flex;
		flex-wrap: wrap;
	}

	tr:last-child .value {
		@include bp($point_4, min) {
			padding-bottom: 3.2rem;
		}

		@include bp($point_4 - 1) {
			padding-bottom: 2.2rem;
		}
	}

	th,
	td {
		width: 100%;
	}
}

table.variations .label {
	color: $gray_2;

	font-weight: 500;
	letter-spacing: 0.12em;
	line-height: 2.4rem;
	text-transform: uppercase;

	@include bp($point_4, min) {
		padding-bottom: 0.6rem;
		font-size: 1.6rem;
	}

	@include bp($point_4 - 1) {
		padding-bottom: 0.8rem;
		font-size: 1.4rem;
	}
}

table.variations .value {
	@include bp($point_4, min) {
		padding-bottom: 2rem;
	}

	@include bp($point_4 - 1) {
		padding-bottom: 1.8rem;
	}
}

.product_content__purchase_type {
	display: flex;
	align-items: center;
	flex-wrap: wrap;

	@include bp($point_4, min) {
		margin: 0 -4.2rem -2rem 0;
	}

	@include bp($point_4 - 1) {
		margin: 0 -2.2rem -1rem 0;
	}
}

.product_content__purchase_type .form_radio {
	@include bp($point_4, min) {
		padding: 0 4.2rem 2rem 0;
	}

	@include bp($point_4 - 1) {
		padding: 0 1.6rem 1rem 0;
	}
}

.product_content__purchase_type .form_radio__label {
	font-weight: 500;

	@include bp($point_4 - 1) {
		font-size: 1.4rem;
	}
}

.product_content__actions {

	@include bp($point_4, min) {
		display: flex;
		flex-wrap: wrap;
		align-items: center;

		&:not(:last-child) {
			margin-bottom: 2.6rem;
		}
	}

	@include bp($point_4 - 1) {
		&:not(:last-child) {
			margin-bottom: 2.2rem;
		}
	}
}

.product_content__actions--out-stock_mod {
	margin-bottom: 0;
}

.single_add_to_cart_button {
	@include bp($point_4, min) {
		width: calc(50% - 1.2rem);
	}

	@include bp($point_4 - 1) {
		width: 100%;
	}
}

.product_content__stock {
	display: flex;
	align-items: center;
	padding-top: 3.6rem;
	border-top: 1px solid $beige_2;
}

.product_content__stock_icon {
	flex-shrink: 0;

	margin-top: -2px;

	img {
		@include contain_img;
	}

	@include bp($point_4, min) {
		width: 5.6rem;
		height: 5.6rem;
		margin-right: 2.2rem;
	}

	@include bp($point_4 - 1) {
		width: 4.6rem;
		height: 4.6rem;
		margin-right: 1.6rem;
	}
}

.product_content__stock_content {
	@include bp($point_2, min) {
		padding-right: 2rem;
	}
}

.product_content__stock_title {
	font-weight: 500;
	color: #044e31;

	@include bp($point_4, min) {
		font-size: 2rem;
		line-height: 1.2;

		&:not(:last-child) {
			margin-bottom: 0.6rem;
		}
	}

	@include bp($point_4 - 1) {
		font-size: 1.4rem;
		line-height: 1.38;
		letter-spacing: -0.01em;

		&:not(:last-child) {
			margin-bottom: 0.3rem;
		}
	}
}

.product_content__stock_text {
	@include bp($point_4, min) {
		font-size: 1.4rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.2rem;
		line-height: 1.16;
	}
}

.out_of_stock {
	display: flex;
	align-items: center;
}

.out_of_stock__icon {
	flex-shrink: 0;
	margin-right: 1.4rem;

	img {
		@include cover_img;
	}

	@include bp($point_4, min) {
		width: 4rem;
		height: 4rem;
	}

	@include bp($point_4 - 1) {
		width: 2.8rem;
		height: 2.8rem;
	}
}

.out_of_stock__title {
	color: #ae1022;
	font-weight: 500;

	@include bp($point_4, min) {
		font-size: 2rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.6rem;
	}
}
